import Vue from 'vue'
import Vuex from 'vuex'

import * as types from './actionType'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    posts: [
      {
        id: 1,
        title: '第一个',
        content: 'jfjknjdsfnjk',
        author: 'jack',
        type: 'ask'
      },
      {
        id: 2,
        title: '第二个',
        content: '阿萨大大大啊阿萨啊 啊啊 阿达',
        author: 'jack',
        type: 'share'
      },
      {
        id: 3,
        title: '第二个',
        content: '阿萨大大大啊阿萨啊 啊啊 阿达',
        author: 'jack',
        type: 'share'
      },
      {
        id: 4,
        title: '第二个',
        content: '阿萨大大大啊阿萨啊 啊啊 阿达',
        author: 'jack',
        type: 'share'
      },
      {
        id: 5,
        title: '第二个',
        content: '阿萨大大大啊阿萨啊 啊啊 阿达',
        author: 'jack',
        type: 'share'
      }
    ],
    tab: 'all',
    isPost: false,
  },
  getters: {
    // 获取过滤后的列表
    getFilterPosts: state => type => {
      return state.posts.filter(item => type === 'all' || item.type === type)
    },
    // 查询列表
    getPosts: state => (types, val) => {
      return state.posts.filter(item => (item[types] + '').includes(val))
    },

    // 获取tab
    getTab: state => state.tab,
    getIsPost: state => state.isPost,
  },
  mutations: {
    // 添加新内容
    [types.ADD_POSTS](state, payload) {
      console.log(state);
      // state = {
      //   ...state,
      //   posts: [...state.posts, payload]
      // }
      state.posts = [...state.posts, payload.post]
      payload.callback('success')
    },
    // 通过id删除
    [types.DEL_POSTSBYID](state, payload) {
      state.posts = state.posts.filter(item => item.id !== payload.id)
      // state = {
      //   ...state,
      //   posts: state.posts.filter(item => item.id === payload)
      // }
    },
    // 设置tab
    [types.SET_TAB](state, payload) {
      // state = {
      //   ...state,
      //   tab: payload.tab
      // }
      state.tab = payload.tab
    },
    [types.SET_ISPOST](state, payload) {
      state.isPost = payload.isPost
    },
  },
  actions: {
    addPosts({ commit }, payload) {
      commit(types.ADD_POSTS, payload)
    },
    delPostsById({ commit }, payload) {
      commit(types.DEL_POSTSBYID, payload)
    },
    setTab({ commit }, payload) {
      commit(types.SET_TAB, payload)
    },
    setIsPost({ commit }, payload) {
      commit(types.SET_ISPOST, payload)
    },
  }
})
