
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
components: {
Tabbar,
TabbarItem,
Actionsheet
},
data() {
return {
show: false
};
},
computed: {
tab() {
return this.$store.getters.getTab;
},
isPost() {
return this.$store.getters.getIsPost;
},
menus() {
return {
all: "全部",
ask: "问答",
share: "分享"
};
}
},
methods: {
handleChangeTab(key) {
this.$store.dispatch("setTab", {
tab: key === 'cancel' ? 'all' : key
});
},
handleClick() {
this.show = true;
}
}
};
